import React, { Component } from 'react';
// import PropTypes from 'prop-types';

import SegmentHeader from '../SegmentHeader';
import CollapseItem from './components/CollapseItem';


import styles from './Faq.module.scss';

const data = [{
  title: '1. Production  how long from start to finish?',
  desc: 'Every client has different needs, making it difficult to generalize a timeframe. However, we aim to deliver your video within 3-4 weeks. Please get in touch with us for a more specific delivery timeline.',
}, {
  title: '2. Production  how do we get underway?',
  desc: 'Contact us now to tell us about your idea, needs, and aspirations. Well respond quickly with a quick yet detailed guideline on what to expect, what well need from you, and how to take the next steps!',
}, {
  title: '3. Costs  how much do your videos cost?',
  desc: 'The cost of producing your video has everything to do with the needs and details of your specific requirements. Contact us with details regarding your project and well reply swiftly with a price estimate.',
}, {
  title: '4. Costs  do you provide revisions, and is there a cost?',
  desc: 'Feel free to ask for revisions at no additional charge  we arent satisfied until you are. However, revisions increase the delivery time. To expedite the process, please consolidate your revisions into a single request.',
}, {
  title: '5. Costs  how does payment work?',
  desc: 'We accept bank transfer for payment and kindly ask that you provide a 50% deposit before we begin the project.',
}, {
  title: '6. Rights  do I hold the rights to the video?',
  desc: 'You are the sole and undisputed owner of your magnificent new video  After finalizing the payment, well transfer the high-resolution video over to you.',
}, {
  title: '7. Collaboration  how do we communicate?',
  desc: 'Options abound for keeping in touch. Well keep to emails or other forms of online communication that allow for file transfers  this way, it is easy to stay on top of files and updates.',
},
// {
//   title: 'Render',
//   desc: 'It is no secret that Facebook prioritizes video in its. It is no secret that Facebook prioritizes video in its algorithm, allowing you to reach up to 50% more  people at lower costs than other advertisers. While your competitors are still using image ads shouting discounts & offers on Facebook, think about what a video ad with can do for your brand?',
// }
]

class Faq extends Component {
  renderData(d, index) {
    return (
      <CollapseItem
        key={`faq-${index}`}
        data={d}
      />
    )
  }

  render() {
    return (
      <div id="faq" className={styles.faq}>
        <SegmentHeader
          title="FAQ"
          subTitle="FAQ"
        />
        <div className={[styles.dataWrapper, 'main-margin'].join(' ')}>
          {data.map(this.renderData)}
        </div>
      </div>
    );
  }
}

Faq.defaultProps = {
};

Faq.propTypes = {
};

export default Faq;
